"use client";

import Link from "next/link";
import { usePathname, useRouter } from "next/navigation";
import { useSelector } from "react-redux";
import { useEffect, useState } from "react";

import { Button } from "@/components/ui/button";

export default function Header() {
  const pathname = usePathname();
  const router = useRouter();

  const settings = useSelector((state) => state.settings?.data || {});
  const siteName =
    settings?.config?.config_name ||
    settings?.config?.config_store ||
    "MyAffiliate";

  const logo =
    "https://weshipfashions.com/image/catalog/logo/weshipfashions-logo.png";

  const [loggedIn, setLoggedIn] = useState(false);
  const [user, setUser] = useState(null);

  // ✅ re-check when route changes (after login redirect)
  useEffect(() => {
    checkSession();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [pathname]);

  async function checkSession() {
    try {
      const res = await fetch(`/api/account/me`, {
        cache: "no-store",
        credentials: "include",
      });
      const data = await res.json().catch(() => ({}));

      if (data?.success && data?.loggedIn) {
        setLoggedIn(true);
        setUser(data.user || null);
      } else {
        setLoggedIn(false);
        setUser(null);
      }
    } catch {
      setLoggedIn(false);
      setUser(null);
    }
  }

  async function logout() {
    await fetch(`/api/account/logout`, {
      method: "POST",
      credentials: "include",
    });

    setLoggedIn(false);
    setUser(null);
    router.push("/account/login");
  }

  // ✅ When logged in, clicking logo goes to /account
  const homeHref = loggedIn ? "/account" : "/";

  return (
    <header className="sticky-top bg-white border-bottom">
      <nav className="navbar navbar-light bg-white py-2">
        <div className="container d-flex align-items-center justify-content-between">

          {/* Brand */}
          <Link href={homeHref} className="navbar-brand d-flex align-items-center gap-2">
            <img src={logo} alt={siteName} className="img-fluid" />
            <span className="d-none d-md-inline fw-bold">{siteName}</span>
          </Link>

          {/* Right actions (ALWAYS visible, mobile + desktop) */}
          <div className="d-flex align-items-center gap-2">
            {!loggedIn ? (
              <>
                <Link href="/account/login" className="d-none d-md-flex text-decoration-none">
                  <Button variant="outline">Login</Button>
                </Link>

                <Link href="/register" className="d-none d-md-flex text-decoration-none">
                  <Button size="sm">
                    Register
                  </Button>
                </Link>
              </>
            ) : (
              <>
                {/* Desktop dropdown */}
                <div className="dropdown d-none d-md-block">
                  <button
                    className="btn btn-outline-primary btn-sm dropdown-toggle px-3"
                    type="button"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                  >
                    {user?.firstname ? `Hi, ${user.firstname}` : "My Account"}
                  </button>

                  <ul className="dropdown-menu dropdown-menu-end">
                    <li>
                      <Link className="dropdown-item" href="/account">
                        Dashboard
                      </Link>
                    </li>
                    <li>
                      <Link className="dropdown-item" href="/account/profile">
                        Profile
                      </Link>
                    </li>
                    <li><hr className="dropdown-divider" /></li>
                    <li>
                      <button className="dropdown-item text-danger" onClick={logout}>
                        Logout
                      </button>
                    </li>
                  </ul>
                </div>

                {/* Mobile menu button */}
                <button
                  className="btn btn-outline-secondary btn-sm d-md-none"
                  type="button"
                  data-bs-toggle="offcanvas"
                  data-bs-target="#mobileMenu"
                >
                  <i className="fa-solid fa-bars"></i>
                </button>
              </>
            )}

            {/* Offcanvas button for non-logged users also (optional) */}
            {!loggedIn ? (
              <button
                className="btn btn-outline-secondary btn-sm d-md-none"
                type="button"
                data-bs-toggle="offcanvas"
                data-bs-target="#mobileMenu"
              >
                <i className="fa-solid fa-bars"></i>
              </button>
            ) : null}
          </div>
        </div>
      </nav>

      {/* Mobile Offcanvas */}
      <div className="offcanvas offcanvas-end" tabIndex="-1" id="mobileMenu">
        <div className="offcanvas-header">
          <h5 className="offcanvas-title">{siteName}</h5>
          <button type="button" className="btn-close" data-bs-dismiss="offcanvas"></button>
        </div>

        <div className="offcanvas-body">
          {loggedIn ? (
            <>
              <div className="mb-3">
                <div className="fw-semibold">
                  {user?.firstname ? `${user.firstname} ${user.lastname || ""}` : "My Account"}
                </div>
                <div className="text-muted small">{user?.email || ""}</div>
              </div>

              <div className="d-grid gap-2">
                <Link className="btn btn-outline-primary" href="/account" data-bs-dismiss="offcanvas">
                  Dashboard
                </Link>

                <Link className="btn btn-outline-primary" href="/account/profile" data-bs-dismiss="offcanvas">
                  Profile
                </Link>

                <button className="btn btn-outline-danger" onClick={logout} data-bs-dismiss="offcanvas">
                  Logout
                </button>
              </div>
            </>
          ) : (
            <div className="d-grid gap-2">
              <Link className="btn btn-outline-primary" href="/account/login" data-bs-dismiss="offcanvas">
                Login
              </Link>
              <Link className="btn btn-primary" href="/register" data-bs-dismiss="offcanvas">
                Register
              </Link>
            </div>
          )}
        </div>
      </div>
    </header>
  );
}